<template>
<div class="address">
    <Head :agood='1' :bgood='bgood'></Head>
      <div class="content">
        <div class="happy">
            <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="text"/>
            <input type="button" value="确认" @click="btn"/>
        </div>
        <div class="yellow">
            为了满足商家的送餐要求，建议您从列表中选择地址
        </div>
        <ul>
            <router-link tag="li" v-for="(item,key) in list" :key="key" :to="'/xin?name='+item.name+'&geohash='+item.geohash">
                <div>{{item.name}}</div>
                <div>{{item.address}}</div>
            </router-link>
        </ul>
    </div>
    
</div>
</template>

<script>
import axios from 'axios'
import Head from '@/components/head/Head'
export default {
    name:'addres',
    components:{Head},
    data(){
        return{
            bgood:'搜索地址',
            text:'',
            list:[]
        }
    },
    mounted(){

    },
    methods:{
        btn(){
            axios.get('http://elm.cangdu.org/v1/pois?type=nearby&keyword='+this.text+'').then((res)=>{
                console.log(res.data)
                this.list=res.data
            })
        },
       
    }
}
</script>

<style lang='scss'>
.content{
    margin-top: .91rem;
    width: 100%;
    height: 100%;
    background: white;
    .happy{
        width: 95%;
        height: 1.2rem;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        
        input{
           
            &:nth-of-type(1){
                font-size: .3rem;
                width: 80%;
                height: .6rem;
                background: #f5f5f5;
                border: none;
                border-radius: 0.05rem; text-indent: 1em;
            }
            &:nth-of-type(2){
                width: 16%;
                height: .6rem;
                margin-left: .2rem;
                background: #3190e8;
                color: white;
                border: none;
                border-radius: 0.05rem;
                font-size: .3rem;
                font-weight: bold;
                
            }
        }
    }
    .yellow{
        width: 100%;
        height: .35rem;
        background: yellow;
        text-align: center;
        font-size: .24rem;
        color: orange;
    }
}
ul{
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: .32rem;
    li{
        height: 1.5rem;
        background: #f5f5f5;
        border-bottom: 0.01rem solid gray;
        div{
            overflow: hidden;
            text-overflow: ellipsisl;
            white-space: nowrap;
            color: #969696;;
            margin-left: .2rem;
            &:nth-of-type(1){
                padding: .2rem 0;

            }
            &:nth-of-type(2){
                padding-bottom: .2rem;

            }
        }
    }
}
</style>


